.input {
  font-family: "Roboto", sans-serif;
  color: black;
  margin: 0px;
  font: optional;
  font-size: 1.5rem;
  width: 70%;
  outline-style: none;
  border-style: none;
  border-radius: 5px;
  padding: 3px 5px;
  margin-left: 5px;
}
.btn {
  margin-left: 1%;
  font-size: large;
  background-color: #1e40af;
  height: 2.3rem;
  width: 4rem;
  border-radius: 5px;

  color: white;
  border: none;
  box-shadow: 1px 1px 3px black;
}
.btn:hover {
  background-color: #19379b;
}
.label {
  font-size: 1.5rem;
  font-weight: bold;
}
