.menu {
  display: flex;
  flex-direction: column;
  height: 100px;
  width: 80px;
}
.btn {
  display: block;

  font-size: large;
  background-color: #1e40af;
  color: white;
  height: 33%;

  border: none;

  box-shadow: 1px 1px 3px black;
}
.btn:hover {
  background-color: #1e3a8a;
}
.btn:disabled {
  background-color: #3b82f6;
}
